@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semiBold.woff2") format("woff2"), url("../fonts/montserrat-semiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

input, textarea, select, button {
  font: inherit; }

a {
  text-decoration: none;
  color: inherit; }

svg {
  fill: currentColor; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  max-width: 1920px;
  min-width: 320px;
  width: 1920px;
  margin: 0 auto;
  padding-right: 363px;
  padding-left: 363px; }
  @media (max-width: 1919px) {
    .container {
      width: 100%;
      padding: 0 20px; }
      .container--header {
        padding: 0; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 15px; }
      .container--header {
        padding: 0; } }

.page {
  height: 100%;
  box-sizing: border-box; }

.page__body {
  margin: 0 auto;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: -webkit-min-content 1fr -webkit-min-content;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Montserrat", Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  background-color: #ffffff;
  color: #2C2E3F;
  font-size: 16px;
  line-height: 1.2; }

.header {
  position: relative;
  display: flex;
  padding-top: 40px;
  padding-bottom: 29px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); }
  @media (max-width: 1919px) {
    .header {
      padding-top: 0;
      padding-bottom: 0; }
      .header--no-js {
        flex-direction: column;
        align-items: flex-start; }
      .header--open {
        flex-direction: column;
        align-items: flex-start; } }

.header__logo {
  width: 117px;
  padding-left: 12px; }
  @media (max-width: 1919px) {
    .header__logo {
      padding: 18px 20px 14px 21px;
      display: block;
      width: 85px; }
      .header--open .header__logo {
        position: relative;
        z-index: 2; } }
  @media (max-width: 767px) {
    .header__logo {
      padding: 18px 20px 14px 17px; } }

.toggle {
  display: none; }
  @media (max-width: 1919px) {
    .header--closed .toggle {
      display: block;
      width: 72px;
      height: 50px;
      padding: 10px 21px;
      background-color: #FE7865;
      border: 0; }
      .header--closed .toggle::before {
        content: "";
        display: block;
        position: absolute;
        width: 22px;
        height: 1px;
        background-color: #ffffff;
        box-shadow: 0 7px #ffffff, 0 13px #ffffff;
        top: 17px;
        right: 25px; }
    .header--no-js .toggle {
      display: none; }
    .header--open .toggle {
      display: block;
      width: 50px;
      height: 52px;
      padding: 15px;
      background-color: #ffffff;
      box-shadow: none;
      border: none; }
      .header--open .toggle::before, .header--open .toggle::after {
        content: "";
        display: block;
        position: absolute;
        width: 22px;
        height: 1px;
        background-color: #d0d0d1;
        transform: rotate(45deg); }
      .header--open .toggle::after {
        transform: rotate(-45deg); } }

.nav {
  width: 770px; }
  @media (max-width: 1919px) {
    .nav {
      width: 100%; }
      .header--open .nav {
        position: absolute;
        background-color: #ffffff; } }

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  min-width: 0;
  word-break: break-word;
  list-style: none;
  justify-content: flex-end; }
  @media (max-width: 1919px) {
    .nav__list {
      padding-top: 84px;
      padding-bottom: 280px; }
      .header--no-js .nav__list {
        flex-direction: column; }
      .header--closed .nav__list {
        display: none; } }

.nav__item {
  margin-right: 40px; }
  .nav__item:last-child {
    margin-right: 0; }
  @media (max-width: 1919px) {
    .nav__item {
      margin-right: 0;
      width: 100%;
      margin-bottom: 30px; }
      .nav__item:last-child {
        margin-bottom: 0; } }

.nav__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #141517; }
  @media (max-width: 1919px) {
    .nav__link {
      font-weight: 600;
      font-size: 20px;
      line-height: 28px;
      color: #48494D;
      padding: 15px 16px 17px 16px;
      background-color: #FBF8F8;
      display: flex;
      justify-content: space-between;
      align-items: center; }
      .nav__link::after {
        content: "";
        display: block;
        position: static;
        width: 25px;
        height: 25px;
        background-image: url("../img/icon-menu-arrow.svg");
        background-repeat: no-repeat; } }

@media (max-width: 1919px) {
  .nav__toggle {
    margin-left: auto; } }

.tel {
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  color: #141517;
  text-align: right;
  padding-right: 23px;
  display: flex;
  padding-left: 55px;
  text-decoration: underline;
  width: 303px; }
  .tel::before {
    content: "";
    display: block;
    position: static;
    width: 30px;
    height: 30px;
    background-image: url("../img/icon-phone.svg");
    background-repeat: no-repeat;
    margin-right: 6px; }
  @media (max-width: 1919px) {
    .tel {
      display: none; } }
